
#body{
    height: 100vh;
    margin:0 auto;
    padding: 0;
    color:#187074;
    background-color: #0D181E;
    overflow:hidden;
    font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
}
#connexionCSS{
  margin: 0 auto;
  position: absolute;
  width: 100px;
  left:calc(50% - 50px);
  top:calc(50% - 50px);
  z-index: 1000
}
#menuBarre{
  height: 10vh;
  background-color: #060E11;
}
.newCSS {
  height: 15px;
  width: 15px;
  background-color: orange;
  position: absolute;
  left: 80%;
  border-radius: 50%;
}
#sectionsCSS{
  float: left;
 
  height: 90vh;
}
#buttonSectionListe{
  list-style: none;
  margin-top: 30px;
}
.buttonRubrique>li>div{
  border-radius: 15px;
  width: 50px;
  height: 50px;
  margin-top: 10px;
  margin-bottom: 10px;
  cursor:pointer;
}
.buttonRubrique>li>div:hover{
  filter: grayscale(100%);
}
.selection{
  filter: grayscale(100%);
}
#globalCSS{
  background-image: url(data:image/png;base64,);
}
#priveCSS{
  background-image: url(data:image/png;base64,);
}
#persoCSS{
  background-image: url(data:image/png;base64,);
}

#bodyListeChat{
    height: 90vh;
    margin:0 auto;
    padding: 0;
    background-color: #16222E
}
.listeSectionsCSS{
    cursor: pointer;
    padding:5px;
    background-color: #16222E;
    border-radius:10px;
    margin-left: -15px;
    z-index: 0;
}
.listeSectionsCSS:hover{
    filter: grayscale(0%);
    background-color: #1F303A
}
.selection{
  filter: grayscale(0%);
  background-color: #1F303A
}
.listeChatCSS {
    background-color: #16222E;
    list-style: none;
    margin-top: 10px;
}
.popupCSS {
    position: absolute;
    margin:auto;
    background-color: #e8d6c9;
    z-index: 1000;
    padding: 20px;
}
.newCSS2 {
    list-style: disc;
    text-transform: uppercase;
    color:orange;
}
#ajouterSectionCSS{
    background-color: #0D181E;
    height: 38px;
    margin-bottom: 15px;
}
#ajouterSectionCSS>img{
    height: 25px;
}
#titreTypeSection{
    text-transform: uppercase;
    text-align: center;
    padding:10px;
    margin: 10px;
    background-color: #0D181E;
    border-radius: 10px;
}
#titreSection{
    position: absolute;
    margin-top: -9vh;
    margin-left:17vw;
    text-align: center;
    z-index: 2;
    color:#157676;
    text-transform: uppercase;
    font-size: 40px;
}
/*tchat*/
.tchatZoneCSS{
  width: 100%;
}
#zoneTchatCSS{
  background-color: #1F303A;
  height: 90vh;
  width: 100%;
  margin:0 auto;
  padding:0;
  z-index: 10;
}
#imgFond{
  height: 100%;
  width: 100%;
  text-align: center;
  line-height: 90vh
}
#zoneTchatCSS>div>img{
  margin:auto;
}
#sectionRecherche{
  width: 110%;
  display: flex;
  margin-left: 3px;
}
#recherchePersoCSS{
background-color: #2A3C48;
  border:none;
  border-radius: 12px;
  color:white; 
  padding:5px;
}
#svg3Points {
  float: right;
  position: relative;
}
#svg3Points svg{
  width: 7px;
  height: 25px;
  color:white;
  float: right;
  z-index: initial;
  filter:initial;
}
#afficherPlus{
  display: none;
  width: 100px;
  z-index: 999;
  background-color: #0D181E;
  position: absolute;
  right: 0px;
  color:white;
}
#svg3Points:hover>#afficherPlus{
  display: block;
  position: absolute;
  z-index: 999;
}
#afficherPlus>p{
  padding: 10px;
}
#afficherPlus>p:hover{
  background-color:#1F303A;
}
b-modal{
  overflow: hidden;
}



#chatBox{
		width: 100%
}
#tchat {
		height: 80vh;
		width: auto;
		overflow-y: scroll;
		margin:auto;
		background-color: #1F303A;
		background-image: url(/static/img/mascotOpacity.d6001e3.png);
		background-repeat: no-repeat;
		background-position: center;
}
#messageCSS {
		padding:5px;
		width: 100%;
		list-style: none;
		color:white;
}
.userCSS {
}
.userCSS{
		color:#1C9EA0;
}
.envoyerCSS {
	margin: 0 auto;
	width: 100%;
	height: 10vh;
	background-color: #111B24;
	line-height: 10vh;
	display: flex;
	justify-content: space-around;
	align-items: center;
	position: relative;
}
.envoyerCSS>img{
	cursor:pointer;
	width: 57px;
    height: 57px;
}
#inputMessage{
	height: 57px;
	width: 72%;
	background-color: #2A3C48;
	border:none;
	border-radius: 12px;
	color:white;
}
#label-file {
    cursor: pointer;
    background-image: url(data:image/png;base64,);
    width: 57px;
    height: 57px;
    margin-bottom: 0.0rem;
}
.input-file {
    display: none;
}
#fileUpload{
	position: absolute;
	left:1%;
	margin-bottom: 15vh;
	padding: 3px;
	padding-left: 15px;
	width: auto;
	height: auto;
	border:1px solid black;
	text-align: right;
	align-items: center;
	line-height: normal;
	background-color: #16222E;
	color:white;
	border-radius: 20px;
}
#progressBar{
	margin: 5px;
}
.messageMoi{
	text-align: right;
	margin: 5px;
	margin-right: 50px;
}
.messageAutre{
	margin:5px;
	margin-left: 15px;
}
#suppFile{
	cursor: pointer;
	margin-left:10px;
	margin-right:5px;
	color:red;
}
#suppFile:hover{
	color:white;
}
#picker{
	z-index: 100000
}
.imgLienMessage{
	height: 300px;
}
.emoji-sizer {
	width:25px;
	height: 25px;
}

/*lien*/

 /* unvisited link */
a:link {
    color: #63C3D1;
}

/* visited link */
a:visited {
    color: #63C3D1;
}

/* mouse over link */
a:hover {
    color: #63C3D1;
}

/* selected link */
a:active {
    color: #63C3D1;
} 



.actifsCSS {
	position: absolute;
}
